@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

#kakashi-face-nav {
    animation: spin 1s linear 1s 2;
    /* animation: spin duration timing-function delay iteration-count direction fill-mode; */
}

nav {
    border: 0px solid orangered;

    width: 100%;
    height: 56px;

    display: flex;
    justify-content: space-between;

    padding: 1rem 5rem;
    color: white;

}

nav h2 {
    margin: 0 0;
    font-family: monospace;

    font-size: 18px;
}

@media (max-width: 600px) {
    nav h2 {
        font-size: 12px;
        border: 0px solid white;
    }

    nav {
        justify-content: space-between;
        padding: 1rem 1.6rem;

        height: 48px;
    }

}

#menu-btn {
    /* height: 18px; */
    /* width: 8px; */

    background: none;
    border: 0px solid white;
}

.menu-board {
    color: white;
    background-color: rgb(21, 36, 36);

    border-bottom: 0px solid white;
    /* border: 1px solid red; */
    padding: 0 16px;

    display: flex;
    flex-direction: column;
    align-items: center;

    position: absolute;
    top: 48px;
    /* right: 0%; */

    width: 99.5%;
}

.menu-board ul {
    list-style-type: none;
    border: 0px solid white;

    width: fit-content;
    padding: 0;
}

.menu-board li {
    border: 0px solid white;
    width: 100%;
    padding: 8px 0;

    font-weight: bold;
    font-size: 22px;
}





nav h2:hover {
    color: lightgreen;
}

nav h2 img {
    height: 18px;
}

nav ul {
    margin: 0 0;
    list-style: none;

    display: flex;
}

nav ul li {
    padding: 0 8px;
}

nav ul li a:hover {
    color: lightgreen;
}

nav ul li a {
    font-size: 14px;
    text-decoration: none;
    color: white;
}

nav ul li a span {
    color: orangered;
}